.container-form {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}

.container-form input {
    width: 200px;
    height: 30px;
    border-radius: 5px;
    outline: none;
    border: .5px solid rgb(192, 190, 190);
    margin-top: 10px;
    padding-left: 5px;
}

.container-form button {
    margin-top: 10px;
}


.container-form span {
    cursor: pointer;
}