.roulette {
    width: 100%;
    z-index: 100;
    transition: all 5s;
    object-fit: cover;
    border-radius: 100%;
}

.vara {
    height: 70px;
    width: 15px;
    background-color: #aaa;
    margin: auto;
    position: relative;
    top: 40px;
    border-radius: 0px 0px 8px 8px;
    border: 1px solid #000;
    border-top: 5px solid #000;
    z-index: 1000;
}

button {
    width: 150px;
    height: 50px;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    background: rgba(255, 55, 55, 0.8);
    color: #fff;
    transition: .3s all ease;
}

button:hover {
    background: rgba(255, 55, 55, 0.5);
}

span {
    cursor: pointer;
}